.delete.btn.btn-default {
  background-color: transparent;
  color: #ff1744;
  font-size: 24px;
  font-style: italic;
  position: relative;
  top: -3px;
  right: 5px;
  cursor: pointer;
}

.save.btn.btn-default {
  background-color: transparent;
  background-color: #315659;
  color: #f3f0dd;
  font-size: 16px;
  position: relative;
  top: -3px;
  right: 5px;
  cursor: pointer;
}

.dropdown-toggle {
  width: 100px;
  height: 20px;
  margin-left: 10px;
}
.dropdown-menu {
  width: 100%;
}

.btn-group img {
  width: 100%;
  height: auto;
  float: left;
  margin-bottom: 5px;
}

.dropdown-menu > li > a:hover {
  background: #f1f9fd;
  width: 100%;
}

/* $search-blue */
.dropdown-header {
  background: #ccc;
  font-size: 14px;
  font-weight: 700;
  padding-top: 5px;
  padding-bottom: 5px;
  margin-top: 10px;
  margin-bottom: 5px;
}

.scrollable-menu {
  width: 100%;
  height: auto;
  max-height: 300px;
  overflow-x: hidden;
}

.pic {
  width: 100px;
  height: 100px;
  padding: 2px;
  margin: 5px;
  float: left;
}

.save-note,
.delete-saved-recipe,
.get-saved-recipe {
  width: 145px;
}

.card-holder {
  display: table;
  margin: 0 auto;
  text-align: center;
  padding: 25px 10px 10px 10px;
}

.open {
  width: 100%;
}

.open > .dropdown-menu {
  width: 100%;
  height: auto;
  display: block;
}

RingLoader {
  margin: 0 auto;
}

.display {
  background-color: #2c3e50;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-align: center;
}
